<template>
  <div v-if="!step == 0" class="header">
    <ul class="header-button-left">
      <li @click="step--">Cancle</li>
    </ul>
    <p class="logo" v-if="step == 1">당신의 MBTI는?</p>
    <p class="logo" v-if="step == 2">템플릿</p>
    <p class="logo" v-if="step == 3">내용&서체</p>
    <ul class="header-button-right">
      <li v-if="step < 3" @click="step++">Next</li>
    </ul>
  </div>

  <div v-if="step == 0">
    <div
      class="profile-image"
      :style="{ backgroundImage: `url(${userProfile})` }"
    ></div>
    <h1>{{ user.nickName }}님의 방명록 페이지</h1>
    <p>방명록 ID: {{ $route.params.id }}</p>
  </div>

  <Container @step="step = $event" @postPublished="fetchPostData" :step="step" :postData="postData" :id="id"/>

  <div class="footer">
    <ul class="footer-button-plus">
      <input type="file" id="file" class="inputfile" />
      <button v-if="step == 0" @click="step = 1">작성하기</button>
    </ul>
  </div>
</template>

<script>
import axios from "axios";
import Container from "./Container.vue";

const VUE_APP_API_URL = process.env.VUE_APP_API_URL;

export default {
  name: "Pages",
  data() {
    return {
      step: 0,
      user: {},
      userProfile: "",
      postData: [],
    };
  },
  methods: {
    async fetchUserData() {
      await axios
        .get(`${VUE_APP_API_URL}/userData/${this.id}`)
        .then((response) => {
          this.user = response.data;
          console.log(this.user);
          this.userProfile =
            VUE_APP_API_URL + "/assets/mbti/" + this.user.mbti + ".png";
        })
        .catch((error) => {
          console.error("Error fetching user data:", error);
        });
    },
    async fetchPostData() {
      await axios
        .get(`${VUE_APP_API_URL}/postData`)
        .then((response) => {
          this.postData = response.data.filter(post => post.postId === this.id);
          // ID를 기준으로 내림차순 정렬
          this.postData.sort((a, b) => b.id - a.id);
        })
        .catch((error) => {
          console.error("Error fetching post data:", error);
        });
    },
  },
  mounted() {
    this.fetchUserData();
    this.fetchPostData();
  },
  props: ["id"], // 라우트 파라미터로 전달된 ID를 프롭스로 받습니다.
  components: {
    Container,
  },
};
</script>

<style>
.profile-image {
  width: 100px;
  height: 100px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  margin: auto;
  display: block;
}
</style>