<template>
  <div v-if="step == 0">
    <div v-if="!postData.length">이런. 친구가 작성해주질 않았나 보네요. 하하</div>
    <div>
      <p>친구한테 님 방명록 페이지 공유하셈</p>
      <p>회원기능이 없어서 메모장에 저장해두는걸 추천</p>
      <button @click="copy">공유하기</button>
    </div>
    <Post :postData="postData[i]" v-for="(post, i) in postData" :key="i" />
  </div>







  <!-- 방명록 작성 -->
  <!-- mbti선택페이지 -->
  <div v-if="step == 1">
    <div class="mbti-selection">
      <button
        v-for="(mbti, index) in mbtiList"
        :key="index"
        :class="{ 'mbti-button': true, selected: selectedMBTI === mbti }"
        @click="selectMBTI(mbti)"
      >
        {{ mbti.toUpperCase() }}
      </button>
    </div>
  </div>


  <div class="black-bg" v-if="modal && !step == 0">
    <div class="white-bg">
      <div>
        상세페이지임
        개쩌는 템플릿 : 200원임
        사셈
      </div>
      <p style="color: red">{{info}}</p>
      <button @click="info = '스토어 준비 중입니다'">구매하기</button>
      <button @click="modal = false; info = ''">닫기</button>
    </div>
  </div>




  <!-- 템플릿선택페이지 -->
  <div v-if="step == 2">
    <div
      class="template-image"
      :style="{ backgroundImage: `url(${selectedTemplate})` }"
    ></div>
    <div class="filters">
      <TemplateBox
        v-for="(template, i) in templates" :key="i" :template="templates[i]" @path="templateUrl($event)">
      </TemplateBox>
    </div>
      <button @click="modal = true">더 많은 템플릿 보러가기</button>
  </div>






  <!-- 글작성페이지 -->
  <div v-if="step == 3">
    <div class="template-imgae-container">
      <div class="template-image" :style="{ backgroundImage: `url(${selectedTemplate})` }">
        <div class="centered-content">
          <p :class="font">{{content}}</p>
        </div>
      </div>
    </div>
    <div class="write">
      <textarea v-model="content" class="write-box" placeholder="내용을 입력해보세요!"></textarea>
    </div>
    <p>서체 선택</p>
    <div>
      <button @click="font = 'wooJu'" class="wooJu">학교안심 우주</button>
      <button @click="font = 'dotUm'" class="dotUm">학교안심 산뜻돋움</button>
      <button @click="font = 'jeMok'" class="jeMok">학교안심 곧은제목</button>
      <button @click="font = 'bangHak'" class="bangHak">학교안심 봄방학</button>
    </div>
    <button @click="modal = true">더 많은 서체 보러가기</button>

    
    <div class="footer">
      <ul class="footer-button-plus">
        <input type="file" id="file" class="inputfile" />
        <button @click="publish()">작성하기</button>
      </ul>
  </div>
  </div>





</template>

<script>
import Post from "./Post.vue";
import TemplateBox from "./TemplateBox.vue";
import axios from "axios";

const VUE_APP_API_URL = process.env.VUE_APP_API_URL;

export default {
  name: "Container",
  data() {
    return {
      mbtiList: [ "istj", "isfj", "infj", "intj", "istp", "isfp", "infp", "intp", "estj", "esfj", "enfj", "entj", "estp", "esfp", "enfp", "entp"],
      templates: [
        { id: 1, name: "템플릿 1", path: VUE_APP_API_URL + '/assets/template/template1.jpeg' },
        { id: 2, name: "템플릿 2", path: VUE_APP_API_URL + '/assets/template/template2.jpeg' },
        { id: 3, name: "템플릿 3", path: VUE_APP_API_URL + '/assets/template/template3.jpeg' },
        { id: 4, name: "템플릿 4", path: VUE_APP_API_URL + '/assets/template/template4.jpeg' },
        { id: 5, name: "템플릿 5", path: VUE_APP_API_URL + '/assets/template/template5.jpeg' },
        { id: 6, name: "템플릿 6", path: VUE_APP_API_URL + '/assets/template/template6.jpeg' },
        { id: 7, name: "템플릿 7", path: VUE_APP_API_URL + '/assets/template/template7.jpeg' },
        { id: 8, name: "템플릿 8", path: VUE_APP_API_URL + '/assets/template/template8.jpeg' },
        // 추가 템플릿들
      ],
      selectedMBTI: "istj",
      selectedTemplate: null,
      content: '',
      font: '',
      templateId: 1,
      modal: false,
      info: '',
    };
  },
  methods: {
    copy() {
      this.$copyText(this.$route.fullPath).then(()=> {
          alert("복사 완료")
        }	
    )},
    selectMBTI(mbti) {
      this.selectedMBTI = mbti;
      console.log("선택된 MBTI:", mbti);
      console.log(this.templates[0].path);
    },
    templateUrl(e) {
      this.templateId = e;
      console.log(this.templateId);
      this.selectedTemplate = this.templates[e - 1].path;
    },
    formatDate(date) {
      const months = [
        "January", "February", "March", "April", "May", "June",
        "July", "August", "September", "October", "November", "December"
      ];
      const month = months[date.getMonth()];
      const day = date.getDate();
      return `${month} ${day}`;
    },
    publish() {
      const date = new Date(); // 현재 날짜와 시간
      const formattedDate = this.formatDate(date);
      var newPost = {
        postId: this.id,
        mbti: this.selectedMBTI,
        templateId: this.templateId,
        date: formattedDate,
        content: this.content,
        font: this.font,
      }
      axios.post(`${VUE_APP_API_URL}/postData`, newPost)
      .then(response => {
          console.log('New post added:', response.data);
          // 추가 후 필요한 상태 초기화 등 수행
          this.$emit('postPublished');
        })
        .catch(error => {
          console.error('Error adding new post:', error);
        });
      this.$emit('step', 0);
    }
  },
  mounted() {
    this.selectedTemplate = this.templates[0].path; // 마운트 시 초기 템플릿 설정
  },
  props: {
    postData: Array,
    step: Number,
    id: String,
  },
  components: {
    Post,
    TemplateBox,
  },
  emits: ['step', 'postPublished'],
};
</script>

